  position: relative;
  overflow: hidden;
  aspect-ratio: 1;
  transition-delay: 3s;

  &__back,
  .back {
    width: 100%;
    height: 100%;
    position: relative;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    inset: 0;

    &__inner {
      width: 100%;
      height: 100%;
    }

    &__image {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

  }

  &__front,
  .front {
    width: 100%;
    height: 100%;
    position: absolute;
    transition: all 0.6s ease;
    transition-delay: 0.15s;
    top: 100%;
    right: 0;
    bottom: 0;
    left: 0;
    inset: 0;
    background-color: $white;
    z-index: 50;

    &--white {
      background-color: $white;
    }

    &--black {
      background-color: $black;
    }

    &__inner {
      padding: 24px;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
    }

    &__title {
      margin-top: 48px;
      margin-bottom: 8px;
    }

    &__title,
    &__subtitle {
      cursor: default;
    }

    &__link {
      align-self: flex-end;
      margin-top: auto;
      display: block;
    }

    &__title,
    &__subtitle,
    &__link {
      opacity: 0;
      position: relative;
      top: 16px;
      transition: all 0.6s ease;
    }

  }

  &:hover {

    .project-tile__front {
      top: 0;
      transition-delay: 0s;
    }


    .front__title,
    .front__subtitle,
    .front__link {
      opacity: 1;
      top: 0;
      transition-delay: 0.3s;
