.reference-slider {
  width: 100%;
  height: auto;
  color: black;

  &__inner {
    position: relative;
    overflow: hidden;
  }

  &__wrapper {
    transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    cursor: default;
  }

  &__slide {
    height: auto;
    font-size: 18px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: grid;
    grid-template-areas:
      "quote quote"
      "author logo";
    grid-template-columns: auto 1fr;
    align-items: center;
    padding: 64px 140px 64px 140px;
  }

  &__quote {
    grid-area: quote;
    margin-bottom: 32px;
    position: relative;

    &::after,
    &::before {
      position: absolute;
      content: "";
      width: 48px;
      height: 48px;
      background-size: contain;
      background-repeat: no-repeat;
      background-size: contain;
    }

    &::before {
      background-image: url('/media/graphics/quotes/quote-start.svg');
      left: -64px;
      top: -16px;
    }

    &::after {
      background-image: url('/media/graphics/quotes/quote-end.svg');
      right: -64px;
      bottom: -16px;
    }

  }

  &__author {
    grid-area: author;
    border-right: 1px solid $white;
    padding-right: 32px;
    display: block;
  }

  &__logo {
    grid-area: logo;
    padding-left: 32px;
    display: block;
    height: 56px;
  }

  &__arrow {
    width: 32px;
    height: 32px;
    z-index: 119;
    position: absolute;
    top: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: $white;
    cursor: pointer;

    .icon {
      font-size: 16px;
      color: $black
    }

    &--next {
      transform: translateY(-50%) rotate(270deg);
      right: 40px;
    }

    &--prev {
      transform: translateY(-50%) rotate(90deg);
      left: 40px;
    }

  }

  @media (max-width: 850px) {

    &__wrapper {
      transition-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
    }

    &__slide {
      padding: 64px;
    }

    &__arrow {
      display: none;
    }

    &__quote {

      &::before {
        left: -64px;
      }

      &::after {
        right: -64px;
      }

    }

  }

  @media (max-width: 650px) {

    &__slide {
      grid-template-areas:
        "quote"
        "author"
        "logo";
      grid-template-columns: auto;
    }

    &__author {
      border-right: 0;
      margin-bottom: 16px;
    }

    &__logo {
      padding-left: 0;
    }

  }

}

.swiper-slide {
  transition: all 0.3s ease;
  opacity: 0;
  transition: all 0.3s ease .0s !important;
  color: transparent;
}

.swiper-slide-active {
  opacity: 1;
  transition: all 0.3s ease .0s !important;
  z-index: 10000;
  transition-delay: .0s;
  color: white;
}
@at-root {
  @font-face {
    font-family: 'swiper-icons';
    src: url('data:application/font-woff;charset=utf-8;base64, ')
      format('woff');
    font-weight: 400;
    font-style: normal;
  }
}

.swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1;
}
.swiper-vertical > .swiper-wrapper {
  flex-direction: column;
}
.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  // transition-property: transform;
  box-sizing: content-box;
}
.swiper-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}
.swiper-pointer-events {
  touch-action: pan-y;
  &.swiper-vertical {
    touch-action: pan-x;
  }
}
.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  // transition-property: transform;
}
.swiper-slide-invisible-blank {
  visibility: hidden;
}
/* Auto Height */
.swiper-autoheight {
  &,
  .swiper-slide {
    height: auto;
  }

  .swiper-wrapper {
    align-items: flex-start;
    // transition-property: transform, height;
  }
}
.swiper-backface-hidden .swiper-slide {
  transform: translateZ(0);
  backface-visibility: hidden;
}
/* 3D Effects */
.swiper-3d {
  &,
  &.swiper-css-mode .swiper-wrapper {
    perspective: 1200px;
  }
  .swiper-wrapper,
  .swiper-slide,
  .swiper-slide-shadow,
  .swiper-slide-shadow-left,
  .swiper-slide-shadow-right,
  .swiper-slide-shadow-top,
  .swiper-slide-shadow-bottom,
  .swiper-cube-shadow {
    transform-style: preserve-3d;
  }
  .swiper-slide-shadow,
  .swiper-slide-shadow-left,
  .swiper-slide-shadow-right,
  .swiper-slide-shadow-top,
  .swiper-slide-shadow-bottom {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 10;
  }
  .swiper-slide-shadow {
    background: rgba(0, 0, 0, 0.15);
  }
  .swiper-slide-shadow-left {
    background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }
  .swiper-slide-shadow-right {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }
  .swiper-slide-shadow-top {
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }
  .swiper-slide-shadow-bottom {
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }
}

/* CSS Mode */
.swiper-css-mode {
  > .swiper-wrapper {
    overflow: auto;
    scrollbar-width: none; /* For Firefox */
    -ms-overflow-style: none; /* For Internet Explorer and Edge */
    &::-webkit-scrollbar {
      display: none;
    }
  }
  > .swiper-wrapper > .swiper-slide {
    scroll-snap-align: start start;
  }
}
.swiper-horizontal.swiper-css-mode {
  > .swiper-wrapper {
    scroll-snap-type: x mandatory;
  }
}
.swiper-vertical.swiper-css-mode {
  > .swiper-wrapper {
    scroll-snap-type: y mandatory;
  }
}
.swiper-centered {
  > .swiper-wrapper::before {
    content: '';
    flex-shrink: 0;
    order: 9999;
  }
  &.swiper-horizontal {
    > .swiper-wrapper > .swiper-slide:first-child {
      margin-inline-start: var(--swiper-centered-offset-before);
    }
    > .swiper-wrapper::before {
      height: 100%;
      width: var(--swiper-centered-offset-after);
    }
  }
  &.swiper-vertical {
    > .swiper-wrapper > .swiper-slide:first-child {
      margin-block-start: var(--swiper-centered-offset-before);
    }
    > .swiper-wrapper::before {
      width: 100%;
      height: var(--swiper-centered-offset-after);
    }
  }

  > .swiper-wrapper > .swiper-slide {
    scroll-snap-align: center center;
  }
}



.service-detail {

  padding-top: 150px;

  h1 {
		margin-bottom: 16px; 
	}

	h3 {
		text-decoration: underline;
		text-decoration-color: $blue;
		text-underline-position: from-font;
	}

}
.service-tile-set {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 1fr;

  & .service-tile:nth-child(2n) {
    .label {
      background-color: $orange
    }

    .label__title {
      color: $white
    }

    .label__icon {
      color: $white
    }
  }


  @media (max-width: 900px) {
    grid-template-columns: repeat(2, 1fr);

    & .service-tile:nth-child(2n) {
      .label {
        background-color: $white
      }

      .label__title {
        color: $black
      }

      .label__icon {
        color: $black
      }
    }

    & .service-tile:nth-child(1),
    & .service-tile:nth-child(4n),
    & .service-tile:nth-child(4n + 1) {
      .label {
        background-color: $orange
      }

      .label__title {
        color: $white
      }

      .label__icon {
        color: $white
      }
    }


  }

  @media (max-width: 600px) {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 32px;

    & .service-tile:nth-child(1),
    & .service-tile:nth-child(4n),
    & .service-tile:nth-child(4n + 1) {
      .label {
        background-color: $white
      }

      .label__title {
        color: $black
      }

      .label__icon {
        color: $black
      }
    }

    & .service-tile:nth-child(2n) {
      .label {
        background-color: $orange
      }

      .label__title {
        color: $white
      }

      .label__icon {
        color: $white
      }
    }

  }

}

