.button {
  min-width: 200px;
  border-radius: 4px;
  border: none;
  outline: none;
  padding: 12px 16px 8px 16px;
  text-decoration: none;
  display: inline-block;
  text-align: center;
  line-height: 1.5;
  background-color: $orange;
  color: $white;

  &--orange {
    background-color: $orange;

    &:hover {
      background-color: darken($orange, 10%)
    }

  }

  &--blue {
    background-color: $blue;

    &:hover {
      background-color: darken($blue, 10%)
    }

  }

}
.heading {
  color: $white;

  &--black {
    color: $black !important;
  }

  &--blue {
    color: $blue !important;
  }

  &--orange {
    color: $orange !important;
  }

}
.image {
	display: block;
	width: 100%;
	height: 100%;
}
.line {
	border-radius: 2px; 
	background-color: $orange;

	&--vertical {
		width: 4px;	
		height: 0px;
		animation: line-vertical-grow 0.8s ease forwards;
	}
	
	&--horizontal {
		width: 0px;
		height: 4px;
		animation: line-horizontal-grow 0.8s ease forwards;
	}

}
.link {
	text-decoration: underline;
	text-decoration-color: $white;
	text-decoration-thickness: 2px;
	text-underline-position: under;
}
.paragraph {

	&--blue { color: $blue; }
	&--black { color: $black; }
	&--white { color: $white; }

}

.quote {
	position: relative;
	max-width: 800px;
	margin: 0 auto;
	
	&__text {
		color: $white;
		display: block;
		padding: 32px 32px 64px 32px;
		text-align: center;
		width: 100%;
	
		&::before, 
		&::after {
			color: $blue;
			position: relative;
			white-space: pre;
		}
		
		&::before {content: '« '; }
		&::after { content: ' »'; }
	
	}

	&__author {
		position: absolute;
		right: 0;
		bottom: 0;
		margin-bottom: 0 !important;

		&::before {
			content: '- '
		}
		
	}

}
.scroller {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__text {
    margin-bottom: 16px;
  }

  &__icon {
    animation: scroller-pulse 1.2s infinite ease-in-out 3.25s;
    cursor: pointer;
    padding: 8px;
  }

}
.video {
  width: 1440px;
  position: relative;
  padding-bottom: calc(820px / 1440px * 100%);
  overflow: hidden;
  max-width: 100%;
  background: $black;

  &__player {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

}